// Using the crypto module to generate UUID (v4-like) for frontend
function generateUUID() {
    return crypto.randomUUID(); // This is supported in modern browsers
}

// Document Ready Event
document.addEventListener("DOMContentLoaded", () => {
    const cookieBanner = document.getElementById("cookieConsent");
    const acceptCookiesButton = document.getElementById("acceptCookies");
    const rejectCookiesButton = document.getElementById("rejectCookies");
    const customizeCookiesButton = document.getElementById("customizeCookies");
    const savePreferencesButton = document.getElementById("savePreferences");
    const cancelPreferencesButton = document.getElementById("cancelPreferences");
    const cookiePreferencesModal = document.getElementById("cookiePreferencesModal");
    const strictlyNecessaryCheckbox = document.getElementById("strictlyNecessary");
    const performanceCheckbox = document.getElementById("performance");
    const functionalCheckbox = document.getElementById("functional");
    const advertisingCheckbox = document.getElementById("advertising");
    const socialMediaCheckbox = document.getElementById("socialMedia");

    // Check if cookies have been accepted previously
    const consentId = getCookie("consentId") || generateUUID(); // Use generateUUID to get a unique consentId
    if (getCookie("cookiesAccepted") === "true") {
        console.log("Cookies already accepted, no need to show banner.");
    } else {
        setTimeout(() => {
            cookieBanner.classList.add("show");
        }, 500); // Delay duration
    }

    // Setting a cookie
    function setCookie(name, value, days) {
        const date = new Date();
        date.setTime(date.getTime() + (days * 24 * 60 * 60 * 1000)); // Expiry in days
        const expires = "expires=" + date.toUTCString();
        document.cookie = `${name}=${value};${expires};path=/`;  // You can set domain, path, and secure flag if needed
    }

    // Function to get a cookie by name
    function getCookie(name) {
        const nameEq = name + "=";
        const cookies = document.cookie.split(';');
        for (let i = 0; i < cookies.length; i++) {
            let c = cookies[i].trim();
            if (c.indexOf(nameEq) === 0) {
                return c.substring(nameEq.length, c.length);  
            }
        }
        return null; 
    }

    // Function to send preferences to the backend
    async function sendPreferencesToDB(preferences) {
        try {
            const response = await fetch("http://localhost:3000/api/save", {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                },
                body: JSON.stringify({
                    consentId, // Use the generated or retrieved consentId
                    preferences,
                }),
            });

            if (!response.ok) {
                throw new Error("Failed to save preferences");
            }

            console.log("Preferences successfully saved to the database");
        } catch (error) {
            console.error("Error saving preferences:", error);
        }
    }

    // Accept all cookies
    acceptCookiesButton.addEventListener("click", () => {
        const preferences = {
            strictlyNecessary: true,
            performance: true,
            functional: true,
            advertising: true,
            socialMedia: true,
        };

        // Store preferences in cookies
        setCookie("cookiesAccepted", "true", 365); 
        setCookie("cookiePreferences", JSON.stringify(preferences), 365); 
        setCookie("consentId", consentId, 365); // Store consentId in cookies

        // Send preferences and location data to the backend
        sendPreferencesToDB(preferences); 
        saveLocationData();  // Save location data when cookies are accepted

        hideBanner();
    });

    // Reject all cookies
    rejectCookiesButton.addEventListener("click", () => {
        const preferences = {
            strictlyNecessary: true, // Strictly Necessary cookies cannot be rejected
            performance: false,
            functional: false,
            advertising: false,
            socialMedia: false,
        };

        // Store preferences in cookies
        setCookie("cookiesAccepted", "false", 365); 
        setCookie("cookiePreferences", JSON.stringify(preferences), 365); 
        setCookie("consentId", consentId, 365); // Store consentId in cookies

        // Send preferences and location data to the backend
        sendPreferencesToDB(preferences); 
        saveLocationData();  // Save location data even if cookies are rejected

        hideBanner();
    });

    // Customize cookie preferences
    customizeCookiesButton.addEventListener("click", (event) => {
        console.log("Customize button clicked");
        event.preventDefault();
        event.stopImmediatePropagation();

        cookiePreferencesModal.classList.add("show");

        // Ensure Strictly Necessary is checked and disabled in the modal
        strictlyNecessaryCheckbox.checked = true;
        strictlyNecessaryCheckbox.disabled = true;
    });

    // Saving custom preferences
    savePreferencesButton.addEventListener("click", () => {
        const preferences = {
            strictlyNecessary: strictlyNecessaryCheckbox.checked, // Always true and disabled
            performance: performanceCheckbox.checked,
            functional: functionalCheckbox.checked,
            advertising: advertisingCheckbox.checked,
            socialMedia: socialMediaCheckbox.checked,
        };

        // Store preferences in cookies instead of localStorage
        setCookie("cookiesAccepted", "true", 365); 
        setCookie("cookiePreferences", JSON.stringify(preferences), 365); // Store preferences for 1 year
        setCookie("consentId", consentId, 365); // Store consentId in cookies

        sendPreferencesToDB(preferences); // Save to database
        hideBanner();
        cookiePreferencesModal.classList.remove("show");
    });

    cancelPreferencesButton.addEventListener("click", () => {
        cookiePreferencesModal.classList.remove("show");
    });

    // Function to hide the cookie banner
    function hideBanner() {
        cookieBanner.classList.add("hide");
        setTimeout(() => {
            cookieBanner.classList.remove("show", "hide");
        }, 500);
    }

    async function saveLocationData() {
        try {
            // Use the consentId instead of static userId
            const locationData = {
                consentId,  // Use dynamic consentId
                ipAddress: "",
                isp: "",
                city: "",
                country: "",
                latitude: null,
                longitude: null,
            };

            // Fetch location details using an IP info API (for example, ipinfo.io)
            const response = await fetch("https://ipinfo.io/json?token=10772b28291307"); // Replace with your API token
            const data = await response.json();

            locationData.ipAddress = data.ip;
            locationData.isp = data.org;
            locationData.city = data.city;
            locationData.country = data.country;

            // If geolocation is available, capture the latitude and longitude
            if (navigator.geolocation) {
                navigator.geolocation.getCurrentPosition((position) => {
                    locationData.latitude = position.coords.latitude;
                    locationData.longitude = position.coords.longitude;

                    // Send location data to the backend
                    sendLocationDataToDB(locationData);
                }, (error) => {
                    console.error("Error fetching geolocation: ", error);
                    sendLocationDataToDB(locationData); // Send data without lat/long if geolocation fails
                });
            } else {
                sendLocationDataToDB(locationData); // Send data without lat/long if geolocation is not supported
            }
        } catch (error) {
            console.error("Error saving location data:", error);
        }
    }

    async function sendLocationDataToDB(locationData) {
        try {
            const response = await fetch("http://localhost:3000/api/location", {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                },
                body: JSON.stringify(locationData),
            });

            if (!response.ok) {
                throw new Error("Failed to save location data");
            }

            console.log("Location data successfully saved to the database");
        } catch (error) {
            console.error("Error saving location data:", error);
        }
    }
});
